import Cookie from 'js-cookie'

export default {
  key: 'settings',
  set: function (value) {
    const defaultValue = {
      darkMode: false,
      randomBck: false
    }
    // 合并默认值
    const dataToStore = value ? { ...defaultValue, ...value } : defaultValue
    // 设置 Cookie
    Cookie.set(this.key, JSON.stringify(dataToStore), { expires: 7 })
  },
  get: function () {
    const cookieValue = Cookie.get(this.key)
    try {
      return cookieValue ? JSON.parse(cookieValue) : null
    } catch (error) {
      console.error('Failed to parse cookie:', error)
      return null
    }
  },
  remove: function () {
    Cookie.remove(this.key)
  }
}
