import request from '@/util/request'
export function getAllUsers () {
  return request.get('user/all')
}
export function login (username, password) {
  return request.post('/user/login', {
    username,
    password
  })
}
export function register (username, password, email, avatar, bio) {
  return request.post('/user/register', {
    username, password, email, avatar, bio
  })
}
export function getUserById (userId) {
  return request.get(`/user/${userId}`)
}
export function getUsersByUsername (username) {
  return request.get('/user/username', {
    params: {
      username
    }
  })
}
export function updateUser (userId, username, password, bio, avatar, email) {
  return request.post('/user/update', {
    username, userId, password, bio, avatar, email
  })
}
export function getUserInfo (userId) {
  return request.get(`/user/userInfo?userId=${userId}`)
}
