<template>
    <div class="user-profile" @click="onClickProfile">
      <!-- 头像部分 -->
      <div class="avatar-container">
        <img
          :src="user.avatar || defaultAvatar"
          alt="Avatar"
          class="avatar"
        />
      </div>

      <!-- 用户信息部分 -->
      <div class="user-info">
        <!-- 显示用户名 -->
        <div v-if="user.username" class="username">{{ user.username }}</div>

        <!-- 显示邮箱 -->
        <div v-if="user.email" class="email">{{ user.email }}</div>

        <!-- 显示简介 -->
        <div v-if="user.bio" class="bio">{{ user.bio }}</div>

        <!-- 显示管理员状态 -->
        <div v-if="user.isAdmin !== null" class="admin-status">
          <span v-if="user.isAdmin">管理员</span>
          <span v-else>普通用户</span>
        </div>

        <!-- 显示创建时间 -->
        <div v-if="user.createdAt" class="created-at">
          注册时间: {{ formatDate(user.createdAt) }}
        </div>

        <!-- 显示最后登录时间 -->
        <div v-if="user.lastLogin" class="last-login">
          最后登录: {{ formatDate(user.lastLogin) }}
        </div>
      </div>
    </div>
  </template>

<script>
export default {
  name: 'UserProfile',
  props: {
    user: {
      type: Object,
      required: true
    },
    enableLink: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data () {
    return {
      defaultAvatar: 'https://www.example.com/default-avatar.png' // 默认头像的URL
    }
  },
  methods: {
    // 格式化日期
    formatDate (date) {
      if (!date) return ''
      const d = new Date(date)
      return `${d.getFullYear()}-${d.getMonth() + 1}-${d.getDate()} ${d.getHours()}:${d.getMinutes()}:${d.getSeconds()}`
    },
    onClickProfile () {
      if (this.enableLink === false) {
        return
      }
      this.$router.push(`/userProfile?userId=${this.user.userId}`)
    }
  }
}
</script>

  <style scoped>
  .user-profile {
    padding: 15px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 100%;
    box-sizing: border-box;
    background-color: transparent;
  }

  .avatar-container {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
  }

  .avatar {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #ddd;
  }

  .user-info {
    text-align: center;
  }

  .username {
    font-size: 18px;
    font-weight: bold;
    color: #333;
  }

  .email, .bio, .admin-status, .created-at, .last-login {
    font-size: 14px;
    color: #666;
    margin: 5px 0;
  }

  @media (max-width: 768px) {
    .avatar {
      width: 60px;
      height: 60px;
    }
    .username {
      font-size: 16px;
    }
    .email, .bio {
      font-size: 12px;
    }
  }
  </style>
