<template>
    <div id="user_display">
      <!-- 未登录状态显示 -->
      <van-empty description="请先登录" v-if="isLoggedin===false">
        <!-- 跳转到登录页面 -->
        <van-button round type="danger" class="bottom-button" @click="$router.push('/login')">去登陆</van-button>
      </van-empty>
      <!-- 已经登录显示 -->
      <div class="content" v-else>
        <!-- 封装用户简介组件快速渲染用户信息 -->
        <user-profile :user="userData" class="animate__animated animate__bounceInUp"/>
        <div>
        </div>
        <!-- 封装用户文章统计信息组件 -->
        <info-card :info="userInfo" class="animate__animated animate__flipInY"/>
        <div class="my-aritcle">
          <span v-for="article in myArticles" :key="article.articleId">
            <!-- 封装用户文章组件 -->
            <article-card :article="article" class="animate__animated animate__flipInY" />
          </span>
        </div>
        <div>
          <!-- 选项按钮 -->
          <van-button type="primary" block round @click="onLogout">退出登录</van-button>
          <van-button type="info" block round @click="$router.push('/editProfile')">编辑信息</van-button>
          <van-button type="warning" block round @click="onClickManager">文章管理</van-button>
        </div>
        <!-- 选项开关 -->
        <van-cell center title="夜间模式">
          <template #right-icon>
            <van-switch v-model="settings.darkMode" size="24" />
          </template>
        </van-cell>
        <van-cell center title="随机背景">
          <template #right-icon>
            <van-switch v-model="settings.randomBck" size="24" />
          </template>
        </van-cell>
      </div>
    </div>
  </template>
<script>
import { getUserId, clearUser } from '@/util/local'
import { getUserById, getUserInfo } from '@/api/user'
import UserProfile from '@/components/UserProfile.vue'
import articleCard from '@/components/articleCard.vue'
import { getArticleByAuthorId } from '@/api/article'
import { Dialog, Toast } from 'vant'
import InfoCard from '@/components/InfoCard.vue'
import Settings from '@/util/settings'
export default {
  name: 'user_display_vue',
  components: {
    UserProfile,
    articleCard,
    InfoCard
  },
  data: () => {
    return {
      isLoggedin: false,
      userData: {},
      myArticles: [],
      userInfo: {},
      settings: Settings.get()
    }
  },
  methods: {
    onLogout () {
      // 确认对话框
      Dialog.confirm({
        title: '退出登录',
        message: '此操作无法撤销'
      })
        .then(() => {
          // on confirm
          clearUser()
          this.$router.replace('/home')
          Toast.loading({
            message: '退出登录'
          })
        })
        .catch(() => {
          // on cancel
        })
    },
    // 暂未实现具体功能
    onClickManager () {
      Toast({
        type: 'fail',
        message: '你没有权限'
      })
    }
  },
  async created () {
    // 通过localstorage检测用户是否已经登录
    if (getUserId() === '') {
      return
    }
    // 请求用户信息，文章信息
    this.isLoggedin = true
    const res = await getUserById(getUserId())
    this.userData = res.data
    const articles = await getArticleByAuthorId(getUserId())
    if (articles.code === 200) {
      this.myArticles = articles.data
    }
    const userInfoRes = await getUserInfo(getUserId())
    this.userInfo = userInfoRes.data
    if (Settings.get() === null) {
      Settings.set({ darkMode: false, randomBck: false })
    }
  },
  watch: {
    settings: {
      handler (newV, oldV) {
        Settings.set(newV)
      },
      deep: true
    }
  }
}
</script>
  <style scoped>

  </style>
