import axios from 'axios'
export async function getImage () {
  let result = {}
  await axios({
    method: 'get',
    url: 'api1/api/img/tu/view'
  }).then(res => {
    // console.log('img:', res)
    result = res
  }, err => {
    // console.log('img:', err)
    result = err
  })
  return result
}
