<script>
import { publishArticle } from '@/api/article'
import { getUserId } from '@/util/local'
import { Notify } from 'vant'
import { getAiAriticle } from '@/api/thirdPartyApi'
export default {
  name: 'publish_vue',
  data: () => {
    return {
      article: {
        authorId: getUserId(),
        title: '',
        content: ''
      },
      hasLoggedin: getUserId()
    }
  },
  methods: {
    async onPublish () {
      if (this.article.title === '' || this.article.content === '') {
        Notify({
          message: '请输入标题和正文',
          color: '#ad0000',
          background: '#ffe1e1'
        })
        return
      }
      const res = await publishArticle(this.article.title, this.article.content, this.article.authorId, 0)
      // console.log(res)
      if (res.code === 200) {
        Notify({
          message: '发布成功',
          color: '#ad0000',
          background: '#ffe1e1'
        })
        this.$router.go(-1)
      }
    },
    async onAiWriting () {
      if (this.title === '') {
        return
      }
      const res = await getAiAriticle(this.title)
      console.log('ai:', res)
    }
  },
  created () {
    // console.log('sss:', typeof this.hasLoggedin, this.hasLoggedin)
  }
}
</script>
<template>
    <div id="publish_vue">
      <van-nav-bar
        title="发布文章"
        left-text="返回"
        left-arrow
        @click-left="$router.go(-1)"
        />
        <van-empty description="请先登录" v-if="hasLoggedin===''">
          <van-button round type="danger" class="bottom-button" @click="$router.push('/login')">去登陆</van-button>
        </van-empty>
        <van-cell-group v-else class="animate__animated animate__bounceInUp">
          <van-field v-model="article.title" label="标题" placeholder="输入标题" required/>
          <van-field
          v-model="article.content"
          rows="10"
          autosize
          label="正文"
          type="textarea"
          maxlength="400"
          placeholder="请输入正文"
          show-word-limit
          required
          />
          <van-button round block type="info" native-type="submit" @click="onPublish">发布</van-button>
          <!-- <van-button round block type="info" native-type="submit" @click="onAiWriting">ai写作</van-button> -->

        </van-cell-group>
    </div>
</template>
