<template>
    <div class="article-card">
      <div class="article-content" @click="onClickArticle">
        <h3 class="article-title">{{ article.title }}</h3>
        <p class="article-excerpt">{{ article.content }}</p>
        <span class="article-views">Views: {{ article.views }}</span>
      </div>
      <button class="edit-button" @click="editArticle" v-show="showEdit===true">编辑</button>
    </div>
  </template>

<script>
import { updateViews } from '@/api/article'
export default {
  name: 'ArticleCard',
  props: {
    article: {
      type: Object,
      required: true
    },
    showEdit: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  methods: {
    editArticle () {
      // 在这里触发编辑逻辑
      // console.log('编辑文章 ID:', this.article.articleId)
      this.$router.push(`/editPassage?id=${this.article.articleId}`)
    },
    async onClickArticle () {
      this.$router.push(`/passage?id=${this.article.articleId}`)
      await updateViews(this.article.articleId)
    }
  }
}
</script>

  <style scoped>
  .article-card {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
    width: 100%;
    box-sizing: border-box;
    margin: 8px 0;
  }

  .article-content {
    overflow: hidden;
    flex-grow: 1;
    margin-right: 10px;
  }

  .article-title {
    font-size: 16px;
    font-weight: bold;
    margin: 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .article-excerpt {
    font-size: 14px;
    color: #555;
    margin: 4px 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 290px
  }

  .article-views {
    font-size: 12px;
    color: #888;
  }

  .edit-button {
    font-size: 14px;
    padding: 6px 12px;
    color: #007aff;
    background: none;
    border: none;
    cursor: pointer;
    outline: none;
  }
  </style>
