import request from '@/util/request'
export function getAllArticles () {
  return request.get('article/all')
}
export function getArticleById (id) {
  return request.get(`article/findbyid?id=${id}`)
}
export function publishArticle (title, content, authorId, views) {
  return request.post('article/publish', {
    title, content, authorId, views
  })
}
export function getArticleByAuthorId (authorId) {
  return request.get(`article/findbyAuthorId?authorId=${authorId}`)
}
export function updateArticle (title, content, articleId) {
  return request.put('article/update', {
    title, content, articleId
  })
}
export function deleteArticle (articleId) {
  return request.post('article/delete', {
    articleId
  })
}
export function updateViews (articleId) {
  return request.get(`article/updateViews?articleId=${articleId}`)
}
export function getArticlesByTitle (title) {
  return request.get(`article/findbyTitle?title=${title}`)
}
export function getArticleByPageOrderedByViewsDESC (size, index, reverse) {
  return request.get(`article/page?size=${size}&index=${index}&order=views&sort=desc&reverse=${reverse}`)
}
export function getArticleByPageOrderedByCreatedTimeASC (size, index, reverse) {
  return request.get('/article/page', {
    params: {
      size,
      index,
      reverse,
      order: 'created_at',
      sort: 'desc'
    }
  })
}
