import { defineStore } from 'pinia'
import { getUser } from '@/util/local'
export const userStore = defineStore({
  id: 'userStore',
  state: () => {
    return {
      user: getUser() || ''

    }
  },
  getters: {
    getUserId () {
      return this.user.userId
    }
  },
  actions: {
    setUserId (id) {
      this.userId = id
    }
  }
})
