import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/view/home'
import Layout from '@/view/layout'
import Publish from '@/view/passage/publish'
import User from '@/view/user/display'
import { getUserId } from '@/util/local'
import { Toast } from 'vant'
const Passage = () => import('@/view/passage/display')
const Search = () => import('@/view/search')
// import { getUserId } from '@/util/local'
const UserLogin = () => import('@/view/user/login.vue')
const UserRegister = () => import('@/view/user/register.vue')
const EditPassage = () => import('@/view/passage/edit')
const SearchResult = () => import('@/view/search/searchResult')
const UserEdit = () => import('@/view/user/edit')
const UserProfile = () => import('@/view/user/profile')
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Layout,
    redirect: '/home',
    children: [
      {
        path: '/home',
        component: Home
      },
      {
        path: '/search',
        component: Search
      },
      {
        path: '/publish',
        component: Publish
      }, {
        path: '/user',
        component: User
      }
    ]
  },
  {
    path: '/login',
    component: UserLogin
  },
  {
    path: '/register',
    component: UserRegister
  },
  {
    path: '/editPassage',
    component: EditPassage
  },
  {
    path: '/passage',
    component: Passage
  },
  {
    path: '/searchResult',
    component: SearchResult
  },
  {
    path: '/editProfile',
    component: UserEdit
  },
  {
    path: '/userProfile',
    component: UserProfile
  }

]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

const authUrls = ['/editProfile', '/editPassage']
router.beforeEach((to, from, next) => {
  // console.log('to:', to.path, 'from:', from.path, 'user ID:', getUserId())

  // 避免从相同的路径导航到相同的路径
  if (to.path === from.path) {
    return next() // 直接阻止导航
  }

  // 判断需要权限验证的路由
  if (authUrls.includes(to.path)) {
    // 如果没有登录，重定向到登录页
    if (getUserId() === '') {
      Toast({
        type: 'text',
        mask: true,
        message: '请先登录',
        position: 'top'
      })
      return next('/login') // 未登录，跳转到登录页
    }
  }

  next()
})

export default router
