<script>
import { updateViews, getArticleByPageOrderedByCreatedTimeASC, getArticleByPageOrderedByViewsDESC } from '@/api/article'
import articleDisplay from '@/components/articleDisplay.vue'
import Settings from '@/util/settings'
import { getImage } from '@/api/thirdPartyApi'
export default {
  name: 'home_vue',
  components: {
    articleDisplay
  },
  data () {
    return {
      articles: [],
      imageUrl: 'http://img5.adesk.com/65def5b0e7bce7143aff65a5?sign=12c5dc08805c7456a387dcf178308ef5&t=6734b611',
      settings: Settings.get(),
      currentPage: 1,
      pageSize: 8,
      totalPages: 5,
      value: 0,
      switch1: false,
      option: [
        { text: '最新发布', value: 0 },
        { text: '最多浏览', value: 1 },
        { text: '最多点赞', value: 2 }
      ]
    }
  },
  methods: {
    // 点击搜索跳转到搜索页
    onSearch () {
      this.$router.push('/search')
    },
    // 点击文字跳转到文章详情页
    async onClickArticle (article) {
      await updateViews(article.articleId)
      this.$router.push(`/passage?id=${article.articleId}`)
    },
    // 分页
    async onChangePage () {
      console.log('index:', this.currentPage, 'size:', this.pageSize, 'change page:', this.currentPage)
      if (this.value === 0) {
        const res = await getArticleByPageOrderedByCreatedTimeASC(this.pageSize, this.currentPage, false)
        if (res.code !== 200) {
          return
        }
        this.articles = res.data.articles
        return
      }
      const res = await getArticleByPageOrderedByViewsDESC(this.pageSize, this.currentPage, false)
      if (res.code !== 200) {
        return
      }
      this.articles = res.data.articles
    },
    onConfirm () {
      this.$refs.item.toggle()
    }
  },
  watch: {
    value: {
      async handler (newV, oldV) {
        if (newV === 0) {
          const res = await getArticleByPageOrderedByCreatedTimeASC(this.pageSize, this.currentPage, false)
          if (res.code !== 200) {
            return
          }
          this.articles = res.data.articles
        }
        if (newV === 1) {
          const res = await getArticleByPageOrderedByViewsDESC(this.pageSize, this.currentPage, false)
          if (res.code !== 200) {
            return
          }
          this.articles = res.data.articles
        }
      }
    }
  },
  async created () {
    const res = await getArticleByPageOrderedByCreatedTimeASC(this.pageSize, this.currentPage, false)
    if (res.code !== 200) {
      return
    }
    this.articles = res.data.articles
    this.totalPages = res.data.totalPages
    if (this.settings.randomBck === true) {
      this.imageUrl = ''
      const BckRes = await getImage()
      this.imageUrl = BckRes.data.url
    }
  },
  computed: {
    preparedStyle () {
      return {
        background: `url(${this.imageUrl})`
      }
    }
  }
}
</script>
<template>
    <div id="home_page" :style="preparedStyle">
        <div class="top_banner">西瓜阅读</div>
        <van-search placeholder="请输入搜索关键词" @click="onSearch" class="animate__animated animate__bounce" />
        <van-dropdown-menu>
          <van-dropdown-item v-model="value" :options="option"/>
      </van-dropdown-menu>
        <div class="articles-container">
          <span v-for="article in articles"   :key="article.articleId" @click="onClickArticle(article)">
              <articleDisplay
              class="animate__animated animate__bounceInUp"
              :title="article.title"
              :author-id="article.authoId"
              :content="article.content"
              :created-at="article.createdAt"
              :updated-at="article.updatedAt"
              :views="article.views"/>
          </span>

        </div>
        <van-pagination v-model="currentPage"
        :total-items="50"
        :show-page-size="5"
        :page-count="totalPages"
        @change="onChangePage"
        >
        <template #prev-text>
          <van-icon name="arrow-left" />
        </template>
        <template #next-text>
          <van-icon name="arrow" />
        </template>
        <template #page="{ text }">{{ text }}</template>
      </van-pagination>
    </div>
</template>
<style scoped>
  .top_banner{
    background-color: #cca8e9;
    text-align: center;
    width: 100%;
    height: 30px;
    color: white;
    font-size: 15px;
    line-height: 30px;
  }
  .articles-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  }
article-detail {
  width: 140px; /* Set fixed width for each article */
  margin-bottom: 20px;
  margin-right: 10px;
}
</style>
